.phone-input {
  * {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1em;
  }
  display: flex;
  margin: auto;
  overflow: hidden;
  border: 1px solid #dbdbdb;
  border-radius: 25px;
  align-items: center;

  .phone {
    background-image: url(data:image/svg+xml;utf8;base64,);
    width: 50px;
    height: 50px;
    background-position: center;
    background-size: 70%;
    background-repeat: no-repeat;
    background-color: #ececec;
    margin-right: 0.5em;
  }

  .flag {
    width: 50px;
    height: 50px;
    font-size: 30px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
  }
  .country-code {
    color: #acacac;
    margin-left: 0.5em;
  }

  input.text-input {
    flex: 1;
    min-width: 0;
    margin-left: 0.5em;
    margin-right: 2px;
    border: 0;
    outline: none;
  }

  .checkmark {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
}
